











































	import header from "../../../components/header.vue"
	export default { 
		components:{
			headers:header
		},
		data(){
			return {
				imgSrc:"../../../static/img/back.png",
				title:"忘记密码",
				user:"",
				pass:""
				// imgSrc:imgs
			}
		},
		computed:{
			isDisabled(){
				if(this.user.length == 11){
					return true;
				}else{
					return false;
				}
			}
		},
		methods:{
			back(){
				uni.navigateBack();
			},
			goInfo(){
				uni.navigateTo({
				    url: '/pages/page/info/info'
				});
			},
			sendyzm(){
				if(!this.vali.is_phone(this.user)){
					uni.showToast({
						icon:"none",
						title: '手机号码格式不正确且只能输入手机号',
						duration: 2000
					});
					this.user = "";
					return false;
				}
				this.yzmTo();
			},
			yzmTo(){
				
				uni.request({
					url: this.userUrl + "/BtCApi/Login/LoginSms", 
					method:'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
					},
					data: {
						Mob: this.user,
						type: 0
					},
					success: (res) => {
						console.log(res.data);
						uni.showToast({
							icon:"none",
							title: res.data.msg,
							duration: 2000
						});
					}
				});
			},
			isLogin(){
				if (!this.vali.is_phone(this.user)) {
                        uni.showToast({
							icon:"none",
                        	title: '手机号码格式不正确',
                        	duration: 2000
                        });
						 return false;
                     }

                     
					 this.login();
			},
			login(){
				const _this = this;
				uni.request({
					url: this.userUrl + "/BtCApi/Login/Verification_mob", //仅为示例，并非真实接口地址。
					method:'POST',
					 header: {
						'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
					},
					data: {
						Mob: this.user,
						Verification_Code: this.pass,
						type:0,
						DeviceId: '',
						PhoneVersion: null,
						ClientVersion: '',
						ClientType: '',
					},
					success: (res) => {
						console.log(res.data);
						if(res.data.status == false){
							uni.showToast({
								icon:"none",
								title: res.data.msg,
								duration: 2000
							});
						}else{
							uni.navigateTo({
								url: `/pages/page/setPassword/setPassword?id=${_this.user}&name=${_this.pass}`
							});
						}
						
					}
				});
			}
		}
	}
